import { fromJS } from 'immutable';
import { nRandomM, indexFor, locationFor } from './utils';

class Game {
  constructor(numRows = 16, numCols = 30, numMines = 99) {
    this.exposed = 0;
    this.gameOver = false;
    this.flagged = 0;
    this.numCols = numCols;
    this.numMines = numMines;
    this.numRows = numRows;
    this.started = false;
    this.time = 0;
    this.board = this.makeBoard();
  }

  makeSolution(startIndex) {
    const { numRows, numCols, numMines } = this;
    const mines = nRandomM(numMines, numRows * numCols, startIndex);
    const solution = Array(numRows * numCols).fill(0);
    mines.forEach((mine) => {
      solution[mine] = 'm';
      const current = locationFor(mine, numCols);
      const neighbors = this.getNeighbors(current);
      neighbors.forEach((location) => {
        if (solution[indexFor(location, numCols)] !== 'm') {
          solution[indexFor(location, numCols)] += 1;
        }
      });
    });
    return fromJS(solution);
  }

  makeBoard() {
    const { numRows, numCols } = this;
    return fromJS(Array.from({ length: numRows }, () => Array(numCols).fill('b')));
  }

  handleRightClick(location) {
    if (!this.gameOver && this.started) {
      if (this.board.getIn(location) === 'f') {
        this.board = this.board.updateIn(location, () => 'b');
        this.flagged -= 1;
      } else if (this.board.getIn(location) === 'b') {
        this.board = this.board.updateIn(location, () => 'f');
        this.flagged += 1;
      }
    }
    return this;
  }

  handleClick(location) {
    const { numRows, numCols, numMines } = this;
    const index = indexFor(location, numCols);
    if (!this.started && !this.gameOver) {
      this.solution = this.makeSolution(index);
      this.started = true;
    }
    if (!this.gameOver && this.board.getIn(location) === 'b') {
      switch (this.solution.get(index)) {
        case 'm':
          this.board = this.board.updateIn(location, () => 'e');
          this.exposeMines(location);
          this.gameOver = 'lose';
          break;
        case 0:
          this.exposeNeighbors(location);
          break;
        default:
          this.expose(location);
          break;
      }
    }
    if (this.exposed === (numRows * numCols) - numMines) {
      this.gameOver = 'win';
      this.exposeMines();
    }
    return this;
  }

  expose(location) {
    const { numCols } = this;
    this.board = this.board.updateIn(location,
      () => this.solution.get(indexFor(location, numCols)));
    this.exposed += 1;
  }

  exposeNeighbors(cell) {
    const { numCols } = this;
    const seen = new Set();
    let queue = [cell];
    while (queue.length) {
      const current = queue.pop();
      const key = JSON.stringify(current);
      if (!seen.has(key) && this.board.getIn(current) === 'b') {
        seen.add(key);
        this.expose(current);
        if (this.solution.get(indexFor(current, numCols)) === 0) {
          queue = [...this.getNeighbors(current), ...queue];
        }
      }
    }
  }

  exposeMines(location) {
    const { numCols } = this;
    this.solution.forEach((v, i) => {
      if (v === 'm' && (!location || i !== indexFor(location, numCols))) {
        this.expose(locationFor(i, numCols));
      }
    });
  }

  getNeighbors([row, col]) {
    const { numRows, numCols } = this;
    const neighbors = [];
    if (row > 0 && col > 0) {
      neighbors.push([row - 1, col - 1]);
    }
    if (row > 0) {
      neighbors.push([row - 1, col]);
    }
    if (row > 0 && col < numCols - 1) {
      neighbors.push([row - 1, col + 1]);
    }
    if (col > 0) {
      neighbors.push([row, col - 1]);
    }
    if (col < numCols - 1) {
      neighbors.push([row, col + 1]);
    }
    if (row < numRows - 1 && col > 0) {
      neighbors.push([row + 1, col - 1]);
    }
    if (row < numRows - 1) {
      neighbors.push([row + 1, col]);
    }
    if (row < numRows - 1 && col < numCols - 1) {
      neighbors.push([row + 1, col + 1]);
    }
    return neighbors;
  }

  tick() {
    if (!this.gameOver && this.started) {
      this.time += 1;
    }
    return this;
  }

  setMines(value) {
    if (!this.started && !this.gameOver) {
      this.numMines = value;
    }
  }

  setRows(value) {
    if (!this.started && !this.gameOver) {
      this.numRows = value;
      this.board = this.makeBoard();
    }
  }

  setCols(value) {
    if (!this.started && !this.gameOver) {
      this.numCols = value;
      this.board = this.makeBoard();
    }
  }
}

export default Game;



